
import Toast from 'vux/src/components/toast/index.vue'

import axios from 'axios'
export default {
name: 'app-mine-forgot-box',
data() {
return {
forgotInfo: {
sendAuthCode: true,
auth_time: 0,
mobile: '',
verifycode: '',
token: ''
}
}
},
methods: {
getAuthCode: function() {
var telReg = /^1[3|4|5|7|8][0-9]{9}$/
if (this.forgotInfo.mobile === '') {
this.$vux.toast.show({
type: 'cancel',
text: '请输入手机号'
})
return 0
}
if (!telReg.test(this.forgotInfo.mobile)) {
this.$vux.toast.show({
type: 'cancel',
text: '手机格式不正确'
})
return 0
} else {
axios.post('http://api.zhimaim.com/api/user/forget_password', { Mobile: this.forgotInfo.mobile })
.then(res => {
console.log(res.data)
if (res.data.success === true) {
this.$vux.toast.show({
text: '验证码已发送'
})
} else {
this.$vux.toast.show({
type: 'cancel',
text: '手机号格式不正确'
})
}
})
.catch(res => {
throw res
})
this.forgotInfo.sendAuthCode = false
this.forgotInfo.auth_time = 60
var authTimetimer = setInterval(() => {
this.forgotInfo.auth_time--
if (this.forgotInfo.auth_time <= 0) {
this.forgotInfo.sendAuthCode = true
clearInterval(authTimetimer)
}
}, 1000)
}
},
handleForgotclick(params) {
var telReg = /^1[3|4|5|7|8][0-9]{9}$/
if (this.forgotInfo.mobile === '') {
this.$vux.toast.show({
type: 'cancel',
text: '请输入手机号'
})
return 0
}
if (!telReg.test(this.forgotInfo.mobile)) {
this.$vux.toast.show({
type: 'cancel',
text: '手机号格式不正确'
})
return 0
}
if (this.forgotInfo.verifycode.trim() === '') {
this.$vux.toast.show({
type: 'cancel',
text: '请输入短信验证码'
})
return 0
}
else {
axios.post('http://api.zhimaim.com/api/user/forget_password_2', {
Mobile: this.forgotInfo.mobile,
VerifyCode: this.forgotInfo.verifycode
})
.then(res => {
if (res.data.success === true) {

window.localStorage.setItem('token',res.data.data.token)
this.$router.push({name: 'appMineRebuild'})
}
})
.catch(res => {
this.$vux.toast.show({
type: 'cancel',
text: '校验失败'
})
throw res
})
}
}
}
}



